<template>
  <div class="row">
    <div class="col-md-12 card p-0">
      <div class="card-body row p-0">
        <div class="col-sm-12">
          <my-filter :rules="rules" v-on:filter-change="updateFilter"></my-filter>
        </div>
        <div class="col-sm-12">
          <my-table :columnDefs="columnDefs" v-bind:data-rows="orders" :actions="actions" :actionsTable="actionsTable" @sortChange="sortChange"/>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import { MessageBox, Message } from 'element-ui'
  import MyTable from 'src/components/UIComponents/Table.vue'
  import MyFilter from 'src/components/UIComponents/Filter.vue'
  import orderSchemas from './order-schemas'
  import dtHelper from 'src/helpers/datatable'

  export default{
    components: {
      MyTable,
      MyFilter
    },
    computed: {
      orders() {
        return this.$store.state.orders;
      }
    },
    data () {
      return {
        filterOutput: [],
        columnDefs: dtHelper.buildColumDefs(orderSchemas),
        actions: [
          {
            type: 'primary',
            icon: 'nc-icon nc-ruler-pencil',
            callback: this.edit
          }
        ],
        filter: {},
        rules: dtHelper.buildRules(orderSchemas),
        actionsTable: [
          {
            title: 'Mới',
            callback: this.statusOrderNew
          },
          {
            title: 'Đã xác nhận',
            callback: this.statusOrderConfirm
          },
          {
            title: 'Chưa thanh toán',
            callback: this.statusPaymentNew
          },
          {
            title: 'Đã thanh toán chưa gửi hàng',
            callback: this.statusPaymentDone
          },
          {
            title: 'Hoàn tất',
            callback: this.statusOrderDone
          },
          {
            title: 'Bị hoàn trả',
            callback: this.statusOrderReturn
          },
          {
            title: 'Bị hủy',
            callback: this.statusOrderCancel
          },
          {
            title: 'Nháp',
            callback: this.statusOrderDraft
          }
        ],
      }
    },
    mounted() {
      window.AP = this;
      this.$store.dispatch('fetchOrders');
      this.$store.dispatch('setPageTitle', 'đơn hàng');
      this.$store.dispatch('setCurrentActions', [{
        label: 'Tạo đơn hàng',
        type: 'primary',
        icon: '',
        callback: this.createOrder
      }])
    },
    methods: {
      updateFilter(filterOutput) {
        let filterString = dtHelper.buildQueryString(filterOutput);
        this.$store.dispatch('fetchOrders', {
          filterString: filterString
        });
      },
      sortChange(data){
        let prop = data.prop;
        let order = data.order == 'ascending' ? 'asc' : 'desc';
        this.$store.dispatch('fetchOrders', {
          order: prop + '=' + order
        });
      },
      createOrder(){
        this.$router.push('/orders/create');
      },
      edit(index, row){
        this.$router.push('/orders/' + row.id);
      },
      statusOrderNew(rows){
        this.updateOrderStatus(rows, 'new', 'order');
      },
      statusOrderConfirm(rows){
        this.updateOrderStatus(rows, 'confirm', 'order');
      },
      statusOrderDone(rows){
        this.updateOrderStatus(rows, 'done', 'order');
      },
      statusOrderReturn(rows){
        this.updateOrderStatus(rows, 'return', 'order');
      },
      statusOrderCancel(rows){
        this.updateOrderStatus(rows, 'cancel', 'order');
      },
      statusOrderDraft(rows){
        this.updateOrderStatus(rows, 'draft', 'order');
      },
      statusPaymentNew(rows){
        this.updateOrderStatus(rows, 0, 'payment');
      },
      statusPaymentDone(rows){
        this.updateOrderStatus(rows, 1, 'payment');
      },
      updateOrderStatus(rows, status, type){
        MessageBox.confirm('Bạn có chắc chắn đổi trạng thái đơn hàng không?', 'Warning', {
          confirmButtonText: 'Đồng ý',
          cancelButtonText: 'Hủy bỏ',
          type: 'warning',
          center: true
        }).then(() => {
          let self = this;
          let arrID = [];
          for (let row of rows) {
            arrID.push(row.id);
          }

          let data = {
            arrId: arrID,
            status: status
          }
          if (type == 'order') {
            this.$store.dispatch('updateStatusOrder', data).then(function (result) {
              self.$store.dispatch('fetchOrders');
              Message({
                type: 'success',
                message: 'Cập nhật thành công'
              });
            }).catch(function (error) {
              Message({
                type: 'error',
                message: error.message
              });
            });
          } else {
            this.$store.dispatch('updateStatusPayment', data).then(function (result) {
              self.$store.dispatch('fetchOrders');
              Message({
                type: 'success',
                message: 'Cập nhật thành công'
              });
            }).catch(function (error) {
              Message({
                type: 'error',
                message: error.message
              });
            });
          }
        })
      }
    },
    destroyed(){
      this.$store.dispatch('setCurrentActions', []);
    }
  }
</script>
<style lang="scss">
  .el-table .td-actions {
    button.btn {
      margin-right: 5px;
    }
  }
</style>
